import * as React from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import { Pagination, Scrollbar } from 'swiper/modules';
import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import 'swiper/css/scrollbar';
import { ProductDelivery } from './ProductDelivery';
import { ProductGaranty } from './ProductGaranty';
import { useParams } from 'react-router-dom';
import { defaultProduct } from '../../../data/defaults';
import { ProductDescription } from './ProductDescription';
import SettingsIcon from '@mui/icons-material/Settings';
import LocalShippingIcon from '@mui/icons-material/LocalShipping';
import DoneAllIcon from '@mui/icons-material/DoneAll';

interface IProps {
    products: IProduct[];
}

const TabPanel = (props: IProps) => {
    const [productsDesc, setProductsDesc] = React.useState<IProduct[]>([]);
    const [productItemDesc, setProductItemDesc] = React.useState<IProduct>(defaultProduct);
    let {id} = useParams<any>();

    const prodDesc = props.products.find(e => e.id === id) ?? defaultProduct;
    React.useEffect(() => {
        const desc = props.products.filter(e => e.id === id) ?? defaultProduct;
        setProductItemDesc(prodDesc);
        setProductsDesc(desc);
    }, [id, prodDesc]);
    return (
        <Swiper
            modules={[Pagination, Scrollbar]}
            spaceBetween={50}
            slidesPerView={1}
            pagination={{ clickable: false }}
            style={{border: '#f2f2f2', borderStyle: 'solid', borderWidth: 2, paddingRight: 10, paddingLeft: 10, paddingTop: 10, paddingBottom: 35, borderRadius: 15}}
        >
            <SwiperSlide>
                <div className='flex items-center justify-center text-center text-base text-[#6a6a6a] font-bold p-1'><SettingsIcon/> Деталі</div>
                <ProductDescription products={productsDesc}/>
            </SwiperSlide>
            <SwiperSlide>
                <div className='flex items-center justify-center text-center text-base text-[#6a6a6a] font-bold p-1'><LocalShippingIcon/> Доставка</div>
                <ProductDelivery/>
            </SwiperSlide>
            <SwiperSlide>
                <div className='flex items-center justify-center text-center text-base text-[#6a6a6a] font-bold p-1'><DoneAllIcon/> Гарантія</div>
                <ProductGaranty/>
            </SwiperSlide>
            
        </Swiper>
    );
};

export default TabPanel;